<template>
  <div id="recycleT">
    <div class="tab_box">
      <Table ref="table" border stripe :columns="columns1" :data="data1"  size="small" :height="tableHeight" ></Table>
    </div>
    <div class="page_box">
      <Page :total="total" :page-size="pageSize" show-elevator size="small" @on-change="pageChange" show-total/>
    </div>
  </div>
</template>

<script>
import { deletedList, recover, deleteForever } from '@/api/systemManage'
export default {
  name: 'RecycleT',
  data () {
    return {
      /* 表格动态高度 */
      tableHeight: Math.floor((window.innerHeight - 270 - 32) / 40) * 40 + 32,
      /* 每页条数 */
      pageSize: Math.floor((Math.floor((window.innerHeight - 270 - 32) / 40) * 40 + 32) / 40),
      /* 页码 */
      pageNum: 1,
      /* 页面总条数 */
      total: 0,
      /* 编辑框默认原数据 */
      editDefaultData: {},
      /* 表格 */
      columns1: [
        {
          title: '界桩编码',
          key: 'jzNumber',
          align: 'center',
          width: 180,
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  color: '#2d8cf0',
                  textDecoration: 'underline'
                },
                on: {
                  click: () => {
                    this.ondbclickTd(params.row.longitude, params.row.latitude, params.row.id)
                  }
                }
              }, params.row.jzNumber)
            ])
          }
        },
        {
          title: '界桩刻号',
          key: 'jzKh',
          align: 'center',
          minWidth: 80
        },
        {
          title: '所属红线',
          key: 'redlineName',
          align: 'center',
          minWidth: 170
        },
        {
          title: '行政区划',
          key: 'placeName',
          align: 'center',
          minWidth: 150
        },
        {
          title: '恢复',
          key: 'isBs',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'primary',
                  size: 'small',
                  icon: 'md-refresh'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要恢复该条数据么？',
                      onOk: () => {
                        this.recover(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '恢复')
            ])
          }
        },
        {
          title: '操作',
          align: 'center',
          minWidth: 80,
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small',
                  icon: 'md-trash'
                },
                on: {
                  click: () => {
                    this.$Modal.confirm({
                      title: '确认',
                      content: '请问您确定要删除该条数据么？',
                      onOk: () => {
                        this.deleteForever(params.row.id)
                      },
                      onCancel: () => {
                      }
                    })
                  }
                }
              }, '删除')
            ])
          }
        }
      ],
      data1: []
    }
  },
  components: {
  },
  mounted () {
    /* 监听浏览器高度 */
    window.onresize = function () {
      this.tableHeight = Math.floor((window.innerHeight - 270 - 32) / 40) * 40 + 32
      this.pageSize = Math.floor((Math.floor((window.innerHeight - 270 - 32) / 40) * 40 + 32) / 40)
    }.bind(this)
    this.getTableData()
  },
  methods: {
    /* axios: 获取列表数据 */
    getTableData () {
      deletedList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum
        }
      }).then((res) => {
        console.log(res)
        if (res.data.code === '0000') {
          this.data1 = res.data.data.rows
          this.total = res.data.data.total
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 删除数据 */
    recover (id) {
      recover({
        data: {
          id: id
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* axios: 恢复数据 */
    deleteForever (id) {
      deleteForever({
        data: {
          id: id
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.getTableData()
          this.$Notice.success({ title: '删除成功' })
        } else {
          this.$Notice.error({ title: '删除失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (p) {
      this.pageNum = p
      this.getTableData()
    }
  }
}
</script>

<style scoped lang="scss">
#recycleT{
  .tab_box{
    padding: 10px;
  }
  .page_box{
    text-align: right;
    padding:0 10px
  }
}
</style>
